
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

export default {
name: 'houseAdd',
components: {
XTextarea,
Group,
Actionsheet,
XInput
},
data () {
return {
aa:'',
value: '',
baseInfo: {},
isShow1: false,
menuList1: [],
isShow2: false,
menuList2: []
}
},
created () {
this.$vux.loading.show({
text: '加载中...'
})
this.getBaseInfo()
},
methods: {
reduceBed () {
if(this.baseInfo.bedNum < 2) {
return
}
this.baseInfo.bedNum--
},
addBed () {
this.baseInfo.bedNum++
},
check (val,type) {
if(!val || val < 0) {
this.baseInfo[type] = 0
}
},
deleteSpecial(index){
this.baseInfo.customProjectInfo.splice(index,1)
},
getEquipment() {
if(this.baseInfo&&this.baseInfo.stockQuantity>99999){
this.$vux.toast.show({
text: '存库不能大于五位数',
position: 'middle',
type: 'warn'
})
return
}
if(this.baseInfo.completeArea&&this.baseInfo.completeArea>99999){
this.$vux.toast.show({
text: '整套面积不能大于五位数',
position: 'middle',
type: 'warn'
})
return
}
this.$vux.loading.show({
text: '加载中...'
})
this.$http.fetchPost('/merchant/room/add/baseInfo', this.baseInfo).then((res)=>{
this.$vux.loading.hide()
this.$router.replace({
name: 'houseEquipment',
query: {
params: {
id: res.data.data.id
}
}
})
})
},
reduce (type) {
if(type == 1) {
if(this.baseInfo.apartmentInfo[0].room_quantity == 0) {
return
}
this.baseInfo.apartmentInfo[0].room_quantity--
}
if(type == 2) {
if(this.baseInfo.apartmentInfo[0].office_quantity == 0) {
return
}
this.baseInfo.apartmentInfo[0].office_quantity--
}
if(type == 3) {
if(this.baseInfo.apartmentInfo[0].kitchen_quantity == 0) {
return
}
this.baseInfo.apartmentInfo[0].kitchen_quantity--
}
if(type == 4) {
if(this.baseInfo.apartmentInfo[0].toilet_quantity == 0) {
return
}
this.baseInfo.apartmentInfo[0].toilet_quantity--
}
},
adder (type) {
if(type == 1) {
this.baseInfo.apartmentInfo[0].room_quantity++
}
if(type == 2) {
this.baseInfo.apartmentInfo[0].office_quantity++
}
if(type == 3) {
this.baseInfo.apartmentInfo[0].kitchen_quantity++
}
if(type == 4) {
this.baseInfo.apartmentInfo[0].toilet_quantity++
}
},
chooseToilet(status) {
if(status == this.baseInfo.toiletStatus) {
this.baseInfo.toiletStatus = 0
return
}
this.baseInfo.toiletStatus = status
},
chooseKitchen(status) {
if(status == this.baseInfo.kitchenStatus) {
this.baseInfo.kitchenStatus = 0
return
}
this.baseInfo.kitchenStatus = status
},
confirm1(menuKey,menuItem) {
menuItem.isSelected = true
this.baseInfo.roomTypeInfo = [menuItem]
this.baseInfo.roomTypeId = menuItem.id
this.isShow1 = false
},
confirm2(menuKey,menuItem) {
menuItem.isSelected = true
this.baseInfo.bedModelInfo = [menuItem]
this.baseInfo.bedModelId = menuItem.id
this.isShow1 = false
},
addSpecial () {
if(this.baseInfo&&this.baseInfo.stockQuantity>99999){
this.$vux.toast.show({
text: '存库不能大于五位数',
position: 'middle',
type: 'warn'
})
return
}
if(this.baseInfo.completeArea&&this.baseInfo.completeArea>99999){
this.$vux.toast.show({
text: '整套面积不能大于五位数',
position: 'middle',
type: 'warn'
})
return
}
this.$vux.loading.show({
text: '加载中...'
})
this.$http.fetchPost('/merchant/room/add/baseInfo', this.baseInfo).then((res)=>{
this.$vux.loading.hide()
this.$router.replace({
name: 'addSpecial',
query: {
params: {
postLongitude: this.baseInfo.longitude,
postLatitude: this.baseInfo.latitude,
id: res.data.data.id
}
}
})
})
},
goNextStep () {
if(!this.baseInfo.detailAddress || !this.baseInfo.houseNumber) {
this.$vux.toast.show({
text: '请输入房屋详细地址',
position: 'middle',
type: 'warn'
})
return
}
if(!this.baseInfo.houseNumber) {
this.$vux.toast.show({
text: '请输入房屋详细地址',
position: 'middle',
type: 'warn'
})
return
}
if(!(this.baseInfo.roomTypeInfo.length > 0 && this.baseInfo.roomTypeInfo[0].isSelected == 1)) {
this.$vux.toast.show({
text: '请选择驿站类型',
position: 'middle',
type: 'warn'
})
return
}
/*if(!(this.baseInfo.apartmentInfo[0].room_quantity > 0 && this.baseInfo.apartmentInfo[0].office_quantity > 0  && this.baseInfo.apartmentInfo[0].kitchen_quantity > 0)){
this.$vux.toast.show({
text: '请输入整套户型信息',
position: 'middle',
type: 'warn'
})
return
}*/
if(!this.baseInfo.stockQuantity) {
this.$vux.toast.show({
text: '请输入房源库存',
position: 'middle',
type: 'warn'
})
return
}
if(this.baseInfo&&this.baseInfo.stockQuantity>99999){
this.$vux.toast.show({
text: '存库不能大于五位数',
position: 'middle',
type: 'warn'
})
return
}
if(!this.baseInfo.completeArea) {
this.$vux.toast.show({
text: '请输入整套面积',
position: 'middle',
type: 'warn'
})
return
}
if(this.baseInfo.completeArea&&this.baseInfo.completeArea>99999){
this.$vux.toast.show({
text: '整套面积不能大于五位数',
position: 'middle',
type: 'warn'
})
return
}
if(!(this.baseInfo.bedNum > 0)){
this.$vux.toast.show({
text: '请输入床位数量',
position: 'middle',
type: 'warn'
})
return
}
if(this.baseInfo.toiletStatus!=1 && this.baseInfo.toiletStatus!=2 && this.baseInfo.toiletStatus!=3){
this.$vux.toast.show({
text: '请完善卫生间信息',
position: 'middle',
type: 'warn'
})
return
}
if(this.baseInfo.kitchenStatus!=1 && this.baseInfo.kitchenStatus!=2 && this.baseInfo.kitchenStatus!=3){
this.$vux.toast.show({
text: '请完善厨房信息',
position: 'middle',
type: 'warn'
})
return
}
this.$vux.loading.show({
text: '加载中...'
})
this.$http.fetchPost('/merchant/room/add/baseInfo', this.baseInfo).then((res)=>{
this.$vux.loading.hide()
this.$router.replace({
name: 'secondStep',
query: {
params: {
postLongitude: this.baseInfo.longitude,
postLatitude: this.baseInfo.latitude,
id: res.data.data.id
}
}
})
})
},
getBack () {
this.$router.go(-1)
},
chooseAddress () {
if(this.baseInfo&&this.baseInfo.stockQuantity>99999){
this.$vux.toast.show({
text: '存库不能大于五位数',
position: 'middle',
type: 'warn'
})
return
}
if(this.baseInfo.completeArea&&this.baseInfo.completeArea>99999){
this.$vux.toast.show({
text: '整套面积不能大于五位数',
position: 'middle',
type: 'warn'
})
return
}
this.$vux.loading.show({
text: '加载中...'
})
this.$http.fetchPost('/merchant/room/add/baseInfo', this.baseInfo).then((res)=>{
this.$vux.loading.hide()
this.$router.replace({
name: 'houseMap',
query: {
params: {
postLongitude: this.baseInfo.longitude,
postLatitude: this.baseInfo.latitude,
id: res.data.data.id
}
}
})
})
},
getBaseInfo () {
let id = ''
if(this.$route.query.params) {
id = this.$route.query.params.id || ''
}
this.$http.fetchGet('/merchant/room/get/baseInfo',{roomId: id,merchantId:this.$store.state.merchantId}).then((res)=>{
setTimeout(() => {
this.$vux.loading.hide()
}, 500)
this.baseInfo = res.data.data
this.baseInfo.merchantId = this.$store.state.merchantId
if(this.$route.query.params && this.$route.query.params.postLocation) {
this.baseInfo.detailAddress = this.$route.query.params.postLocation
this.baseInfo.latitude = this.$route.query.params.postLatitude
this.baseInfo.longitude = this.$route.query.params.postLongitude
}
this.menuList1 = res.data.data.roomTypeInfo
this.menuList1.map((item)=>{
item.label = item.name
if(item.isSelected){
this.baseInfo.roomTypeInfo = [item]
this.baseInfo.roomTypeId = item.id
}
})
this.menuList2 = res.data.data.bedModelInfo
this.menuList2.map((item)=>{
item.label = item.name
if(item.isSelected){
this.baseInfo.bedModelInfo = [item]
this.baseInfo.bedModelId = item.id
}
})

let facilitiesInfo = []
this.baseInfo.facilitiesInfo.map((item)=>{
let flag = false
let childList = []
item.facilities.map((child)=>{
if(child.isSelected == 1){
flag = true
childList.push(child)
}
})
if(flag) {
item.facilities = childList
facilitiesInfo.push(item)
}
})
this.baseInfo.facilitiesInfo = facilitiesInfo


})
}
}
}



